import { createTheme, ThemeProvider } from '@mui/material';
import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';

const theme = createTheme({
  customPalette: {
    primary: {
      main: "#67917D",
    },
  },
  // palette: {
  //   primary: {
  //     main: "#67917D",
  //   },
  // },
});

ReactDOM.render(
  <React.StrictMode>
    <ThemeProvider theme={theme}>
      <App />
    </ThemeProvider>
  </React.StrictMode>,
  document.getElementById('root')
);
